.App {
  font-family: sans-serif;
  text-align: center;
  user-select: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: rgb(142, 199, 252);
  height: 100vh;
  width: 100vw;
}

body {
  margin: 0;
}

.PokedexBody {
  box-shadow: inset -8px -13px 2px 3px #00000038;
  background: linear-gradient(45deg, #d82222, #ff5454eb);
  width: 650px;
  aspect-ratio: 6/7;
  overflow: hidden;
  margin-top: 2.5vh;
  margin-left: 1.25vw;
  border-radius: 20px;
  border: 4px solid rgba(90, 90, 90);
  box-sizing: border-box;
}

.PokedexUpperBody {
  margin-top: 18px;
  display: flex;
  width: 200px;
  justify-content: space-evenly;
}

.Lens {
  background: cyan;
  width: 64px;
  height: 64px;
  border-radius: 100%;
  border: 4px solid white;
  box-sizing: border-box;
}

.Light {
  background: cyan;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  border-left: 2.75px solid rgba(90, 90, 90);
  border-top: 0.5px solid rgba(90, 90, 90);
  border-bottom: 0.5px solid rgba(90, 90, 90);
  box-sizing: border-box;
}

.Red {
  background: rgb(235, 26, 26);
}
.Yellow {
  background: rgb(223, 223, 22);
}
.Cyan {
  background: rgb(17, 218, 218);
}

.WeirdDivider {
  display: flex;
  overflow: hidden;
}

.WeirdDivider > .Piece-1 {
  border-bottom: 4px solid rgba(90, 90, 90);
  width: 60%;
}

.WeirdDivider > .Piece-2 {
  width: 15%;
  border-bottom: 4px solid rgba(90, 90, 90);
  border-right: 5px solid rgba(90, 90, 90);
  transform: translateX(-7px) skew(-45deg);
}

.WeirdDivider > .Piece-3 {
  border-top: 4px solid rgba(90, 90, 90);
  width: 25%;
}

.VisorOuter {
  background: rgb(243, 243, 243);
  padding: 25px;
  margin: 25px;
  height: 400px;
  border: 4px solid rgba(90, 90, 90);
  border-radius: 10px;
}

.VisorInner {
  background: rgb(68, 68, 250);
  border: 4px solid rgba(90, 90, 90);
  height: 350px;
  overflow-y: scroll;
  overflow-x: hidden;
  display: flex;
  flex-wrap: wrap;
  border-radius: 8px 8px 0 8px;
}

.VisorInner > div {
  cursor: pointer;
  width: 90px;
  color: rgb(223, 223, 223);
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: fit-content;
  padding: 1em;
  padding-top: 0em;
}

.VisorInner > p > img {
  cursor: pointer;
}

.ButtonWrapper {
  display: flex;
  flex-direction: row;
  float: right;
  border-collapse: collapse;
}

.ButtonWrapper > button,
input {
  background: rgb(255, 255, 255);
  border: 0;
  border-bottom: 3.5px solid rgba(90, 90, 90);
  padding: 1em;
  cursor: pointer;
}

.Active {
  background: #e5e5e5;
  -webkit-box-shadow: inset 0px 0px 27px 2px rgba(0, 0, 0, 0.199);
  -moz-box-shadow: inset 0px 0px 27px 2px rgba(0, 0, 0, 0.199);
  box-shadow: inset 0px 0px 27px 2px rgba(0, 0, 0, 0.199);
  outline: none;
}

.ButtonWrapper > *:first-child {
  border-left: 1px solid rgba(90, 90, 90);
  border-bottom: 3.5px solid rgba(90, 90, 90);
  border-radius: 0px 0px 0px 25px;
}

*:focus {
  outline: none;
}

.ButtonWrapper > *:last-child {
  border-right: 1px solid rgba(90, 90, 90);
  border-bottom: 3.5px solid rgba(90, 90, 90);
  border-radius: 0px 0px 25px 0px;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f100;
  border-left: 1px solid rgba(255, 255, 255, 0.11);
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
